.home {
  h2 {
    text-align: center;
    font-size: 2em;
    margin: 1em 0;

    @include media-query('med'){
      font-size: 2.5em;
    }
  }

  .front-logo-top {
    @include media-query('med'){
      margin-top: -3em;
    }

    @include media-query('lg'){
      margin-top: -4em;
    }
  }

}


