.woocommerce.archive {
  #body #layout {
    display: flex;
    flex-direction: column-reverse;
    float: none;

    @include media-query('med'){
      display: block;
    }
  }

  .page-title {
    text-align: center;

    @include media-query('med'){
      text-align: left;
    }
  }
}



